<style src="./address.css"></style>
<template src="./address.html"></template>
<script>
import ProviderDialog from "@/resources/provider/provider-dialog";

export default {
  name: "address",
  components: {},
  data() {
    return {
      operacao: {}
    };
  },

  created() {
    let query = this.$route.query.operacao;
    this.operacao = query ? JSON.parse(query) : query;
  },
  mounted() {},
  methods: {
    endereco: function() {
      this.$router.push("/home/address-create");
    },
    register: function() {
      let myThis = this;
      let rota = "/";
      let urlPath =  this.URL_BANK_DEMO +  "/operacoes/" +  this.operacao._id +  "/autorizacao";
      let autorizacao = {
        operacaoAutorizada: true,
        dataHoraAutorizacao: new Date()
      };

      this.$http
        .post(urlPath, autorizacao)
        .then(response => {
          let operacao = response.data;
          myThis.$router.push("/home/form-detail");
        })
        .catch(error => {
          console.log(error);
          this.$router.push(rota);
        });
    }
  }
};
</script>



