<style src="./form-payment.css"></style>
<template src="./form-payment.html"></template>
<script>
export default {
  name: "form-payment",
  components: {},
  data() {
    return {
      operacao : {
        valor : 0 
      }
    }
  },

  created() {
    
  
  },
  mounted() {
    this.$root.$emit("menu_superior_titulo", "Pagamento");
 
  },
  methods: {
    next: function(){
         this.$router.push({
            path: "/home/address",
            query: { operacao: JSON.stringify(this.operacao) }
          });
    }
  }
};
</script>



